import React, { useEffect } from "react";
import history from "../../history";
import Datetime from 'react-datetime';

export default function CreatePage() {
  return (
    <div className="container-fluid" style={{}}>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Sửa thông báo</h4>
      </div>
      <div className="card shadow mb-4">
        <div className="card-body">
          <section className="content">
            <div className="row">
              <div className="col-md-12 col-xs-12">
                <div id="messages" />
                <div className="box">
                  <form role="form" method="post">
                    <div className="box-body">
                      <div className="form-group">
                        <label htmlFor="product_name">Tiêu đề</label>
                        <input
                          type="text"
                          className="form-control"
                          id="txtTitle"
                          name="title"
                          placeholder="Nhập tiêu đề"
                          autoComplete="off"
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Mô tả</label>
                        <input
                          type="text"
                          className="form-control"
                          id="description"
                          name="description"
                          placeholder="Nhập mô tả"
                          autoComplete="off"
                        />
                      </div>
                  
                      <div className="form-group">
                        <label htmlFor="product_name">Kiểu thông báo</label>
                        <select
                          name="type_schedule"
                          id="input"
                          className="form-control"
                        >
                          <option value={0}>Một lần</option>
                          <option value={1}>Hàng ngày</option>
                          <option value={2}>Hàng tuần</option>
                          <option value={3}>Hàng tháng</option>
                        </select>
                      </div>
                        <div className="form-group">
                          <label htmlFor="product_name">
                            Thời gian thông báo trong ngày
                          </label>
                          <Datetime value={"19/09/2022"} timeFormat = {false} dateFormat ="DD/MM/YYYY" />
                        </div>
                      </div>
                  </form>
                  <div className="box-footer">
                    <button type="submit" className="btn btn-info   btn-sm">
                      <i className="fas fa-save" /> Tạo
                    </button>
                    <button
                                                              onClick={()=>{history.goBack()}}

                      type="button"
                      className="btn btn-warning   btn-sm"
                      style={{ marginLeft: "10px" }}
                    >
                      <i className="fas fa-arrow-left" /> Trở về
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}
